<template>
    <PageBg bgImage="/android-chrome-512x512.png" />
    <h1 class="text-center mt-5 display-1 brand">ðŸ¤‘ CrypTea</h1>
    <transition name="fade" mode="out-in">
        <p class="lead text-center" :key="currentWord">{{ currentWord }}</p>
    </transition>
</template>

<script>
import PageBg from '@/components/PageBg.vue';
export default {
    name: 'App',
    components: {
        PageBg,
    },
    data() {
        return {
            words: [
                "Welcome to CrypTea - Your Cryptocurrency Hub",
                "Stay updated with the latest crypto news",
                "Trade your favorite cryptocurrencies with ease",
                "Discover new investment opportunities",
                "Track real-time prices and market trends",
                "Securely store your digital assets",
                "Join a community of crypto enthusiasts",
                "Learn about blockchain technology",
                "Get insights from industry experts",
                "Make informed decisions with our analytics tools"
            ],
            currentIndex: 0,
        };
    },
    computed: {
        currentWord() {
            return this.words[this.currentIndex];
        },
    },
    mounted() {
        setInterval(() => {
            this.currentIndex = (this.currentIndex + 1) % this.words.length;
        }, 4000);
    },
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
    opacity: 0;
}
</style>
